export class SpotInterval extends Record {
    "constructor"(startTime, endTime, price) {
        super();
        this.startTime = startTime;
        this.endTime = endTime;
        this.price = price;
    }
}

export function SpotInterval$reflection() {
    return record_type("SpotMapper.Computation.SpotInterval", [], SpotInterval, () => [["startTime", class_type("System.DateTime")], ["endTime", class_type("System.DateTime")], ["price", float64_type]]);
}

export function getPrice(s) {
    const matchValue = split(s, [","], void 0, 0);
    if ((!equalsWith((x, y) => (x === y), matchValue, defaultOf())) && (matchValue.length === 4)) {
        const st = matchValue[0];
        const p = matchValue[2];
        const et = matchValue[1];
        const price = parse(p) * 0.001;
        return new SpotInterval(now(), now(), price);
    }
    else {
        const clo = toFail(printf("Invalid format: %s"));
        return clo(s);
    }
}

export function getUsage(s) {
    const matchValue = split(s, [";"], void 0, 0);
    if ((!equalsWith((x, y) => (x === y), matchValue, defaultOf())) && (matchValue.length === 6)) {
        const st = matchValue[0];
        const et = matchValue[1];
        const _us = matchValue[2];
        return parse(replace(matchValue[3], ",", ".")) * 1;
    }
    else {
        const clo = toFail(printf("Invalid format: %s"));
        return clo(s);
    }
}

export function compute(allUsages, allPrices) {
    return sum(map((tupledArg) => ((getPrice(tupledArg[0]).price + (1.9 * 0.01)) * getUsage(tupledArg[1])), zip(skip(1, split(allPrices, ["\n"], void 0, 0)), skip(1, split(allUsages, ["\n"], void 0, 0)))), {
        GetZero: () => 0,
        Add: (x, y) => (x + y),
    });
}

